<template>
    <div class="todo-item" v-bind:class="{'is-complete': todo.completed}">
          <div>
          <button @click= "$emit('del-todo')" class = "del">X</button>
          </div>
          <div class = "todo-list">
               <Checkbox :completed="todo.completed"  v-on:checkbox-status="markComplete" />
            {{todo.title}}
          </div>
    </div>
</template>

<script>
import Checkbox from './Checkbox.vue'

export default {
    name: "TodoItem",
    props: ["todo"],
    components: {
        Checkbox
    },
    methods: {
        markComplete(){
            this.todo.completed = !this.todo.completed;
        },
    }
    
}
</script>

<style>

.todo-list {
    margin: 0 auto;
}

.todo-item {
    background: #f4f4f4;
    padding: 10px;
    border-bottom: 1px #ccc dotted;
}

.is-complete {
    text-decoration: line-through;
     text-decoration-color: red;
}

.del {
    background: #ff0000;
    color: #fff;
    border: none;
    padding: 5px 9px;
    border-radius: 50%;
    cursor: pointer;
    float: left;
    margin: 0 50px 0 0;
}
</style>